'use client';

import { useState, useEffect } from 'react';
import {
  startBattle,
  executeTurn,
  getBattle,
  getBattleIdCounter,
} from '@/lib/contract';
import { Battle } from '@/types/game';

interface BattleArenaProps {
  connectedAddress: string | null;
  onMessageChange: (message: string) => void;
}

export default function BattleArena({
  connectedAddress,
  onMessageChange,
}: BattleArenaProps) {
  const [opponentAddress, setOpponentAddress] = useState('');
  const [betAmount, setBetAmount] = useState('0.01');
  const [currentBattle, setCurrentBattle] = useState<Battle | null>(null);
  const [battleId, setBattleId] = useState<bigint | null>(null);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (connectedAddress) {
      loadActiveBattle();
    }
  }, [connectedAddress]);

  const loadActiveBattle = async () => {
    if (!connectedAddress) return;
    try {
      const counter = await getBattleIdCounter();
      if (counter && typeof counter === 'bigint' && counter > 0n) {
        // Check the most recent battle
        const recentBattleId = counter - 1n;
        const battleData = await getBattle(recentBattleId);
        const battle = battleData as Battle;

        if (
          battle.isActive &&
          (battle.player1.toLowerCase() === connectedAddress.toLowerCase() ||
            battle.player2.toLowerCase() === connectedAddress.toLowerCase())
        ) {
          setCurrentBattle(battle);
          setBattleId(recentBattleId);
        }
      }
    } catch (error) {
      console.error('Failed to load active battle:', error);
    }
  };

  const handleStartBattle = async () => {
    if (!opponentAddress.trim()) {
      onMessageChange('Please enter opponent address');
      return;
    }

    setLoading(true);
    try {
      await startBattle(opponentAddress as `0x${string}`, betAmount);
      onMessageChange('Battle started!');
      setOpponentAddress('');
      setBetAmount('0.01');
      // Reload to get the new battle
      setTimeout(() => loadActiveBattle(), 2000);
    } catch (error) {
      onMessageChange('Failed to start battle');
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  const handleExecuteTurn = async () => {
    if (battleId === null) {
      onMessageChange('No active battle');
      return;
    }

    setLoading(true);
    try {
      await executeTurn(battleId);
      onMessageChange('Turn executed!');
      // Reload battle data
      setTimeout(async () => {
        const battleData = await getBattle(battleId);
        setCurrentBattle(battleData as Battle);
      }, 2000);
    } catch (error) {
      onMessageChange('Failed to execute turn');
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="bg-white/10 backdrop-blur-md rounded-lg p-6">
      <h2 className="text-2xl font-bold text-white mb-4">‚öîÔ∏è Battle Arena</h2>

      <div className="space-y-4">
        <div className="bg-white/5 rounded-lg p-4">
          <h4 className="text-lg font-bold text-white mb-2">
            Start New Battle
          </h4>
          <input
            type="text"
            value={opponentAddress}
            onChange={(e) => setOpponentAddress(e.target.value)}
            placeholder="Opponent address (0x...)"
            className="w-full p-3 rounded-lg bg-white/20 text-white placeholder-white/60 border border-white/20 mb-3"
          />
          <input
            type="number"
            value={betAmount}
            onChange={(e) => setBetAmount(e.target.value)}
            placeholder="Bet amount (RON)"
            step="0.01"
            min="0.01"
            max="1"
            className="w-full p-3 rounded-lg bg-white/20 text-white placeholder-white/60 border border-white/20 mb-3"
          />
          <button
            onClick={handleStartBattle}
            disabled={loading || !opponentAddress.trim()}
            className="w-full bg-red-600 hover:bg-red-700 text-white font-bold py-3 px-6 rounded-lg transition-colors disabled:opacity-50"
          >
            {loading ? 'Starting Battle...' : 'Start Battle'}
          </button>
        </div>

        {currentBattle && (
          <div className="bg-white/5 rounded-lg p-4">
            <h4 className="text-lg font-bold text-white mb-2">Active Battle</h4>
            <div className="space-y-2 text-sm text-white/80">
              <p>Player 1 Health: {currentBattle.player1Health.toString()}</p>
              <p>Player 2 Health: {currentBattle.player2Health.toString()}</p>
              <p>
                Current Turn:{' '}
                {currentBattle.currentTurn === connectedAddress
                  ? 'Your turn'
                  : "Opponent's turn"}
              </p>
              <p>Turn Count: {currentBattle.turnCount.toString()}</p>
            </div>
            {currentBattle.currentTurn === connectedAddress && (
              <button
                onClick={handleExecuteTurn}
                disabled={loading}
                className="w-full mt-3 bg-green-600 hover:bg-green-700 text-white font-bold py-2 px-4 rounded-lg transition-colors disabled:opacity-50"
              >
                {loading ? 'Executing...' : 'Execute Turn'}
              </button>
            )}
          </div>
        )}
      </div>
    </div>
  );
}
